import React, { Component } from 'react'
import Ctab from './Ctab'
import './Css/tao.css'
export default class Che extends Component {
    constructor(props) {
	    super(props);
	    this.state = {
	        url: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3410585734,1618955914&fm=26&gp=0.jpg',
	    };
	} 
	render() {
		return (<div>
			<img src={this.state.url} style={{width:'100%'}}/>
			<button className="fanhui" onClick={() => this.props.history.goBack()}> &lt; </button>
			<Ctab />
		</div>)
	}
}
