import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './Css/tao.css'
class Ctab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
		console.log(pathname)
        return (
            <div style={ {   
				position: 'fixed',  
				width: '100%',  
				bottom: 50 } }
			>
            <TabBar 
				unselectedTintColor="#949494" 
				tintColor="red" 
				barTintColor="white" 
			>	
                    <TabBar.Item
                        title="店铺"
                        key=""
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        
                        selected={ pathname === '' }
                        onPress={ () => {
                            this.props.history.push('');
                        } }
                    > </TabBar.Item>
                    <TabBar.Item
						title="客服"
						key=""
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        
                        selected={ pathname === '' }
                        onPress={ () => {
                            this.props.history.push('')
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang1'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        title="收藏"
                        key=""
                    > </TabBar.Item>
                    <TabBar.Item 
                        icon={<i className='iconfont ' ></i> }
						selectedIcon={
						    <i className='iconfont'></i>                           
						}
                        title="加入购物车"
                        key=""
                        selected={ pathname === '' }
                        onPress={ () => {
                            this.props.history.push('')
                        } }
                    ></TabBar.Item>
					
					
					<TabBar.Item
					    icon={<i className='iconfont'></i> }
						selectedIcon={
						    <i className='iconfont'></i>                           
						}
					    title="立即购买"
					    key=""
					    selected={ pathname === '' }
					    onPress={ () => {
					        this.props.history.push('')
					    } }
					></TabBar.Item>
				</TabBar>
            </div>
        );
    }
}
export default withRouter(Ctab);