import './App.css';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import { BrowserRouter } from 'react-router-dom'
import Home from './compoent/Home'
import About from './compoent/About'
import Mytab from './compoent/Mytab'
import Order from './compoent/Order'
import My from './compoent/My'
import Search from './compoent/Search'
import Che from './compoent/Che'
function App() {
  return (
    <BrowserRouter>
      <Switch>
      <Route path='/Home' component={Home}/>      
      <Route path='/About' component={About}/> 
      <Route path='/Order' component={Order}/>
      <Route path='/My' component={My}/>
      <Route path='/Search' component={Search} />
      <Route path='/Che' component={Che} />
      <Route render={()=><Redirect to='/Home'/>} />
     </Switch>
      <Mytab/>
    </BrowserRouter>
  );
}

export default App;
